"use strict";

var dispatcher = dispatcher;
var constants = constants;
var ctx = ctx;

var contentsActions = {
	newItem: function newItem() {

		dispatcher.dispatch({
			action: constants.NEW_ITEM_ADDED
		});
	},
	deleteItem: function deleteItem(id) {

		ctx.deleteItem(id).then(function () {

			dispatcher.dispatch({
				action: constants.ITEM_DELETED,
				data: id
			});
		});
	},
	setSelectedFolder: function setSelectedFolder(id) {

		dispatcher.dispatch({
			action: constants.SELECTED_FOLDER_CHANGED,
			data: id
		});
	},
	setSelectedItem: function setSelectedItem(item) {

		dispatcher.dispatch({
			action: constants.SELECTED_ITEM_CHANGED,
			data: item
		});
	}
};